  handleBalance (event) {
    event.preventDefault();
    const { whoIs } = this.state.TreasuryContract;
    const {TID: tid } = this.state;
    const { getGBalance } = this.state.BankContract;
    whoIs (tid, (err,result) => {
      if (err) alert('Error', err);
      else { getGBalance (result[3], (err, answer) => {
        if (err) alert('Error', err);
        else {alert("Balance of "+answer+"Gr")}
        })
      }})
  }

  handleQuery () {
    const { owesWhat } = this.state.TreasuryContract;
    const {TID: tid} = this.state;
    owesWhat (tid, (err,result) => {
      if (err) alert('Error', err);
      else {alert("Taxpayer owes "+result+"Gr")}
    })
  }

  handleReserves() {
    const { getReserve } = this.state.BankContract;
    getReserve ((err, result) => {
      if (err) alert('No result', err);
      else {alert("GRand Reserves: "+result)}
    })
  }

  handleCirculation() {
    const { getCirc } = this.state.BankContract;
    getCirc ((err, result) => {
      if (err) alert('No result', err);
      else {alert("GRand in Circulation: "+result)}
    })
  }

  handleXchange() {
    const { getXchange } = this.state.BankContract;
    getXchange ((err, result) => {
      if (err) alert('No result', err);
      else {alert(" One GRand buys "+result+" Rands")}
    })
  }

  handleXchangeChange (event) {
    const { setXchange } = this.state.BankContract;
    const {Rate: r} = this.state;
    setXchange (r, (err,result) => {
      if (err) alert('Error', err);
      else {alert("New rate is "+r+" Rands to the GRand")}
    })
  }

